.banner{
    width: inherit;
    padding: 348px 405px 352px 992px;
    background: url(https://github.com/hexschool/webLayoutTraining1st/blob/master/week3/banner.png?raw=true) no-repeat;
    color: #650300;
    p{
        max-width: 576px;
        line-height: 1.4;
        font-size: $fz36;
        white-space: nowrap;
        text-align: right;
    }
    p:nth-child(1){
        padding-left: 64px;
    }
    p:nth-child(2){
        font-size: 64px;
        font-weight: bold;
    }
    @media (max-width:768px){
        padding: 372px 0;
        background: url(https://github.com/hexschool/webLayoutTraining1st/blob/master/week3/banner-sm.png?raw=true) no-repeat;
        padding-left: 150px; //為何這個在376px會沒吃到?        
        p{
            font-size: $fz20;
        }
        p:nth-child(1){
            padding-left: 0px;
        }
        p:nth-child(2){
            font-size: $fz48;
        }
    }
    @media (max-width:376px) {
        padding: 164px 0;
        background-size: 375px 400px;
        padding-right: 15px;
        p{
            font-size: 16px;
        }
        p:nth-child(2){
            font-size: $fz32;
        }
    }
}
.promise{
    padding-top: 60px;
    padding-bottom: 80px;
    background-color: #FBF2F2;
    font-weight: bold;
    color: #650300;
    h2{
        text-align: center;
        margin-bottom: 48px;
        font-size: $fz48;
        line-height: 1.45;
    }
    ul{
        display: flex;
        justify-content: center;
        li{
            max-width: 255px;
            margin-right: 30px;
            text-align: center;
            img{
                height: 88px;
                width: 88px;
            }
            h3{
                font-size: 24px;
                line-height: 1.45;
                margin-top: 27px;
                margin-bottom: 8px;
            }
            p{
                line-height: 1.5;
                text-align: left;
                color: #000;
            }
        }
    }
    @media (max-width:768px) {
        padding: 40px 76px 56px 77px;
        h2{
            margin-bottom: 40px;
            font-size: $fz32;
            line-height: 1.43;
        }
        ul{
            flex-wrap: wrap;
            li:nth-child(n+3){
                margin-top: 68px;
            }
        }
    }
    @media (max-width:376px) {
        padding: 32px 55px 40px 60px;
        h2{
            font-size: $fz24;
            margin-bottom: 28px;
            white-space: nowrap;
        }
        ul{
            margin-left: 35px;
            li:nth-child(n+2){
                margin-top: 40px;
            }
            li p{
                width: 255px;
            }
        }
    }
}
.classic{
    padding: 60px 405px 80px 405px;
    font-weight: bold;
    color: #650300;
    h2{
        text-align: center;
        margin-bottom: 48px;
        font-size: $fz48;
        line-height: 1.45; 
    }
    ul{
        li a{
            display: flex;
            align-items: center;
            color: $layoutBgColor;
            font-size: $fz48;
            line-height: 1.3;
            font-style: italic;
            img{
                width: 635px;
                height: 320px;
            }
            p{
                margin-left: 45px;
                display: flex;
                align-items: center;
            }
        }
        li:nth-child(1) a p::before{
            content: '';
            height: 48px;
            width: 48px;
            display: inline-block;
            margin-right: 15px;
            background: url(https://upload.cc/i1/2021/07/18/x4XuPO.png) no-repeat;
        }
        li:nth-child(2) {
            a{
                margin: 32px 0;
                flex-direction: row-reverse;
                p{
                    margin-right: 30px;
                    margin-left: 0;
                }
                p::before{
                    content: '';
                    height: 48px;
                    width: 48px;
                    display: inline-block;
                    margin-right: 15px;
                    background: url(https://raw.githubusercontent.com/hexschool/webLayoutTraining1st/102c91f7b3d8e08005073f54b3283b09a18dc427/week3/icon-sun.svg) no-repeat;  
                }
            }
        }
        li:nth-child(3) a p::before{
            content: '';
            height: 48px;
            width: 48px;
            display: inline-block;
            background: url(https://raw.githubusercontent.com/hexschool/webLayoutTraining1st/102c91f7b3d8e08005073f54b3283b09a18dc427/week3/icon-thunder.svg) no-repeat;
        }
    }
    @media (max-width:768px) {
        padding: 40px 87px 56px 39px;
        h2{
            margin-bottom: 32px;
            font-size: $fz32;
            line-height: 1.43; 
        }
        ul{
            li a{
                font-size: 28px;
                line-height: 1.3;
                img{
                    width: 390px;
                    height: 168px;
                }
                p{
                    margin-left: 30px;
                }
            }
            li:nth-child(1) a p::before{
                background-size: 36px 36px;
                margin-right: 0;
            }
            li:nth-child(2) {
                a{
                    margin: 8px 0;
                    flex-direction: row;
                    p{
                        margin-left: 30px;
                    }
                    p::before{
                        background-size: 36px 36px;
                        margin-right: 0;
                    }
                }
            }
            li:nth-child(3) a p::before{
                background-size: 36px 36px;
            }
        }
    }
    @media (max-width:376px) {
        padding: 32px 15px 40px 15px;
        h2{
            margin-bottom: 20px;
            font-size: $fz20;
            line-height: 1.45; 
        }
        ul{
            li a{
                img{
                    display: none;
                }
                p{
                    width: 345px;
                    height: 134px;
                    margin-left: 30px;
                    text-align: center;            
                }
            }
            li:nth-child(1) a p{
                background: url(https://github.com/hexschool/webLayoutTraining1st/blob/master/week3/product-1.png?raw=true);
                background-size: 345px 134px;
            }
            li:nth-child(2) a p{
                background: url(https://github.com/hexschool/webLayoutTraining1st/blob/master/week3/product-2.png?raw=true);
                background-size: 345px 140px;
            }
            li:nth-child(3) a p{
                background: url(https://github.com/hexschool/webLayoutTraining1st/blob/master/week3/product-3.png?raw=true);
                background-size: 345px 134px;
            }
        }
    }
}

    
